
import React from 'react';
import vcs from '@/services/VoiceChatService';

import { useDispatch } from 'react-redux';
import { setChatInputType, setChatInputPayload } from '@/store/slices/chatSlice';

const INPUT_STRING = '[INPUT]';

const ChociesInput = ({ payload }) => {
  const dispatch = useDispatch();
  const { choices, format } = payload;

  const formatAndSend = input => {
    vcs.send_text_message(format.replaceAll(INPUT_STRING, input));

    // Reset input to default
    dispatch(setChatInputType(null));
    dispatch(setChatInputPayload({}));
  };

  // Render
  return (
    <div className="ChatPanel-buttons">
      {Object.keys(choices).map((input, idx) => (
        <button key={idx} className="ChatPanel-option" onClick={() => formatAndSend(input)}>{choices[input]}</button>
      ))}
    </div>
  );
};

export default ChociesInput;