import React, { useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import vcs from '@/services/VoiceChatService';
import './BasicAssistant.css';
import cfg from '@/config'

const { agent_image } = cfg;

// const mic = '/assets/images/mic.svg';
const mic = process.env.REACT_APP_URL + '/assets/images/mic.svg';


const BasicAssistant = ({ voiceOnly }) => {
  const { images, state } = useSelector((state) => state.assistant);

  const assistantStatic = images.png || (process.env.REACT_APP_URL + `/assets/images/${agent_image ?? process.env.REACT_APP_AGENT_IMAGE ?? 'agent-3'}.png`);
  const assistantTalking = images.gif || (process.env.REACT_APP_URL + `/assets/images/${agent_image ?? process.env.REACT_APP_AGENT_IMAGE ?? 'agent-3'}.gif`);

  const isRecordingRef = useRef(false); // Use a ref to track recording state across renders

  const handlePress = async (e) => {
    e.preventDefault(); // Prevents unwanted long-press behavior
    if (state.is_muted || voiceOnly || isRecordingRef.current) return; // Prevent starting recording if already recording or voiceOnly is true
    try {
      vcs.start_recording();
      isRecordingRef.current = true; // Mark as recording
    } catch (error) {
      console.error('Error starting recording:', error);
      vcs.stop_recording();
    }
  };

  const handleRelease = () => {
    if (state.is_muted || voiceOnly || !isRecordingRef.current) return; // Prevent stopping if not recording or voiceOnly is true
    vcs.stop_recording();
    isRecordingRef.current = false; // Mark as stopped
  };

  useEffect(() => {
    if (voiceOnly) {
      // Start recording automatically if voiceOnly is true
      if (!isRecordingRef.current) {
        vcs.start_recording();
        isRecordingRef.current = true; // Mark as recording
      }
    } else {
      // Ensure recording is stopped when voiceOnly is false
      if (isRecordingRef.current) {
        vcs.stop_recording();
        isRecordingRef.current = false; // Mark as stopped
      }
    }

    // Cleanup the recording when the component unmounts or voiceOnly changes
    return () => {
      if (isRecordingRef.current) {
        vcs.stop_recording();
        isRecordingRef.current = false;
      }
    };
  }, [voiceOnly]);

  return (
    <div className="BasicAssistant">
      <img
        src={state.is_speaking ? assistantTalking : assistantStatic}
        alt="Assistant"
        className="BasicAssistant-image"
      />
      {!voiceOnly && (
        <button
          className="BasicAssistant-mic-button"
          onTouchStart={handlePress}
          onTouchEnd={handleRelease}
          onMouseDown={handlePress}
          onMouseUp={handleRelease}
        >
          <img src={mic} alt="Mic" className="BasicAssistant-mic-icon" draggable="false" />
        </button>
      )}
    </div>
  );
};

export default BasicAssistant;
