import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import Slideup from '@/components/util/Slideup/Slideup';
import Trigger from './components/Trigger/Trigger';
import Container from './components/Container/Container';

import vcs from '@/services/VoiceChatService';
import is from '@/services/InputService';

import { setConnected, setFailed, setError, setMessage } from '@/store/slices/connectionSlice';
import { setChatInputType, setChatInputPayload, addMessage } from '@/store/slices/chatSlice';
import { setSpeaking, setRecording, setImages, setTriggerActive } from '@/store/slices/assistantSlice';

// A reusable conditional wrapper component.
const ConditionalWrapper = ({ condition, wrapper, children }) =>
  condition ? wrapper(children) : children;

export default function EAgent({ trigger = true, chat = true ,voiceOnly }) {
  const dispatch = useDispatch();
  const { trigger: triggerState } = useSelector((state) => state.assistant);

  useEffect(() => {

    // ----------------------
    // Register VoiceChatService Callbacks
    // ----------------------
    vcs.on_chat_input = ({ type, ...payload }) => {
      dispatch(setChatInputType(type));
      dispatch(setChatInputPayload(payload));
    };

    vcs.on_panel_input = is.set_input;
    vcs.on_message = (message) => dispatch(addMessage(message));
    vcs.on_speak_start = () => dispatch(setSpeaking(true));
    vcs.on_speak_end = () => dispatch(setSpeaking(false));
    vcs.on_record_start = () => dispatch(setRecording(true));
    vcs.on_record_stop = () => dispatch(setRecording(false));

    // -------------------
    // Setup WS Connection
    // -------------------
    vcs.setup_ws();

    // -------------------
    // Bind WebSocket send methods
    // -------------------
    is.ws_send = vcs.ws_send;
    is.send_text_message = vcs.send_text_message;
  }, [dispatch]);

  return (
    <ConditionalWrapper
      condition={trigger}
      wrapper={(children) => (
        <Slideup
          Trigger={Trigger}
          active={triggerState.active}
          setActive={(state) => dispatch(setTriggerActive(state))}
        >
          {children}
        </Slideup>
      )}
    >
      <Container chat={chat} voiceOnly={voiceOnly} />
    </ConditionalWrapper>
  );
}
