import { WavRecorder, WavStreamPlayer } from "wavtools";

/**
 * VoiceChatService - Handles WebSocket communication, audio recording, 
 * playback, and text-based chat interactions.
 */
class VoiceChatService {
  /**
   * Initializes the VoiceChatService.
   * - Sets up WebSocket connection.
   * - Initializes audio recording and playback tools.
   * - Defines callback functions for various states.
   */
  constructor(channel =null, setup_ws=false) {
    // WebSocket
    this.ws = null;
    if(setup_ws) this.setup_ws(channel);

    // Audio
    this.init_recorder();
    this.init_player();

    // State
    this.track_id = 1;
    this.is_recording = false;
    this.is_muted = false;
    this.is_speaking = false;

    // Callbacks
    this.on_chat_input = console.log;
    this.on_panel_input = console.log;
    this.on_message = console.log;
    this.on_speak_start = console.log;
    this.on_speak_end = console.log;
    this.on_record_start = console.log;
    this.on_record_stop = console.log;
    this.on_clear = console.log;

    // Chat
    this.message = '';
    this.chat_input = null;
    this.input = null;

    // Timeout
    this.last_tick_time = 0;        // Updated each tick
    this.timeout_threshold = 60000; // Timeout in ms (5 minutes here)

    // Bindings
    this.ws_send = this.ws_send.bind(this);
    this.ws_send_raw = this.ws_send_raw.bind(this);
    this.send_text_message = this.send_text_message.bind(this);
    this.reset_on_timeout = this.reset_on_timeout.bind(this);
  }

  /**
   * Sets up WebSocket connection with given configurations.
   *
   * @param {number} ws_port - WebSocket port.
   * @param {string} ws_host - WebSocket host.
   * @param {string} ws_path - WebSocket path.
   * @param {string} ws_protocol - WebSocket protocol.
   */
  setup_ws(
    ws_port = process.env.REACT_APP_WS_PORT ?? 6123,
    ws_host = process.env.REACT_APP_WS_HOST,
    ws_path = "/",
    ws_protocol = process.env.REACT_APP_WS_PROTOCOL ?? "ws://"
  ) {
    this.ws = new WebSocket(`${ws_protocol}${ws_host}:${ws_port}${ws_path}`);
    this.ws.onopen = () => console.log("WebSocket connected");
    this.ws.onclose = () => console.log("WebSocket disconnected");
    this.ws.onerror = console.error;
    this.ws.onmessage = this.handle_ws_message.bind(this);
  }

  /**
   * Initializes audio recording and playback.
   */
  async setup() {
    await this.recorder.begin();
    await this.player.connect();
    setInterval(() => {
      this.update_speaking_status(this.player.getFrequencies().values.some((v) => v > 0));
      this.reset_on_timeout();
    },100);
  }

  /**
   * Handles incoming WebSocket messages.
   *
   * @param {MessageEvent} event - WebSocket message event.
   */
  handle_ws_message(event) {
    this.tick();
    this.last_response_timestamp = Date.now();
    let data = event.data;

    try {
      const command = JSON.parse(data);
      if (command.type) {
        switch (command.type) {
          // chat input
          case "ui.chat":
            return this.on_chat_input(command.payload);

          // input panel input
          case "ui.input":
            return this.on_panel_input(command.payload);

          // clear chat history
          case "ui.clear":
            return this.on_clear();
        
          // message part
          case "text.delta":
            this.message += command.message ?? command.delta;
            return
          
          // commit current message
          case "text.completed":
            this.on_message({
              message: this.message,
              from: 'bot',
            });
            this.message = '';
            return
            
          // commit user's message
          case "transcription":
            this.on_message({
              message: command.message,
              from: 'user',
            });
            return

          default:
            console.log("Unprocessed command")
        }
      }

      if (command === "interrupt") return this.audio_interrupt();
    } catch {

      if (this.is_base64(data)) {
        if (!this.is_recording) this.audio_play(this.base64_to_int16(data));
        return;
      }

      if (data === "interrupt") return this.audio_interrupt();
    }
  }

  /**
   * Checks if WebSocket is ready.
   * @returns {boolean} - True if WebSocket is open.
   */
  ws_is_ready() {
    return this.ws?.readyState === WebSocket.OPEN;
  }

  /**
   * Sends a raw WebSocket message.
   * @param {any} data - Data to send.
   */
  ws_send_raw(data) {
    this.tick();
    this.last_response_timestamp = Date.now();
    try {
      this.ws.send(data);
    } catch (error) {
      console.error(error);
      // @dj: fire Swal error
    }
  }

  /**
   * Sends a WebSocket message as JSON.
   * @param {Object} payload - Message payload.
   */
  ws_send(payload) {
    this.ws_send_raw(JSON.stringify(payload));
  }

  /**
   * Starts audio recording.
   */
  async start_recording() {
    // state
    if (this.is_muted) return;
    this.is_recording = true;
    
    
    this.player.interrupt(); // Clear audio
    this.ws_send({ type: "command", command: "response.cancel" }); // Cancel current response
    this.on_record_start(); // callback

    // record & send
    await this.recorder.record((data) => {
      if (this.ws_is_ready()) this.ws_send_raw(data.mono);
    });
  }

  /**
   * Stops audio recording.
   */
  async stop_recording() {
    // state
    this.is_recording = false;

    this.on_record_stop(); // callback
    
    // stop & commit
    await this.recorder.pause();
    if (this.ws_is_ready()) {
      this.ws_send({ type: "command", command: "commit" });
    }
  }

  /**
   * Plays back received audio buffers.
   *
   * @param {Int16Array} audio_buffers - Audio data.
   */
  audio_play(audio_buffers) {
    this.player.add16BitPCM(audio_buffers, this.get_track_id());
  }

  /**
   * Interrupts audio playback.
   */
  audio_interrupt() {
    this.player.interrupt();
  }

  /**
   * Generates a unique track ID for audio playback.
   *
   * @returns {string} - Track ID.
   */
  get_track_id() {
    return String(this.track_id++);
  }

  /**
   * Mutes or unmutes the microphone.
   *
   * @param {boolean} is_muted - True to mute, false to unmute.
   */
  mute(is_muted = true) {
    if (is_muted) this.stop_recording();
    this.is_muted = is_muted;
  }

  
  /**
   * Sends a text message.
   *
   * @param {string} text - Message text
   */
  send_text_message(text){
    this.ws_send({
      'type': 'command',
      'command': 'text',
      'payload': text,
    });
    this.on_message({
      message: text,
      from: 'user',
    });
  }

  /**
   * Changes language settings.
   *
   * @param {string} code - Language code.
   */
  set_language(code) {
    this.ws_send({ type: "command", command: "lang", payload: code });
  }

  /**
   * Updates speaking status and triggers callbacks.
   *
   * @param {boolean} speaking - True if speaking, false otherwise.
   */
  update_speaking_status(speaking) {
    if (speaking !== this.is_speaking) {
      speaking ? this.on_speak_start() : this.on_speak_end();
    }
    this.is_speaking = speaking;
  }

  /**
   * Checks if a given string is Base64 encoded.
   *
   * @param {string} data - Input string.
   * @returns {boolean} - True if valid Base64, else false.
   */
  is_base64(data) {
    return /^[A-Za-z0-9+/]+={0,2}$/.test(data) && data.length % 4 === 0;
  }

  /**
   * Converts a Base64 string to an Int16Array.
   *
   * @param {string} base64_string - Base64 encoded string.
   * @returns {Int16Array} - Decoded audio data.
   */
  base64_to_int16(base64_string) {
    return new Int16Array(Uint8Array.from(atob(base64_string), (c) => c.charCodeAt(0)).buffer);
  }

  /**
   * Updates the current tick time.
   */
  tick() {
    this.last_tick_time = new Date();
  }

  /**
   * Checks if the time since `timeout_reference` exceeds threshold.
   * If so, sends a 'reset' command and reloads the page.
   */
  reset_on_timeout() {
    if (!this.last_tick_time) return;

    const now = new Date();
    const elapsed = now - this.last_tick_time;
    if (elapsed >= this.timeout_threshold) {
      this.ws_send({ type: "command", command: "reset" });
      window.location.reload();
    }
  }

  /**
   * Initializes audio player.
   *
   * @param {number} sampleRate - The audio sample rate (default 24000 Hz).
   */
  init_player(sampleRate = 24000) {
    this.player = new WavStreamPlayer({ sampleRate });
    this.player.connect();
  }

  /**
   * Initializes Audio Recorder
   *
   * @param {number} sampleRate - The audio sample rate (default 24000 Hz).
   */
  init_recorder(sampleRate = 24000) {
    this.recorder = new WavRecorder({ sampleRate });
  }

}

const vcs = new VoiceChatService();
await vcs.setup();
window.vcs = vcs;
export default vcs;


