import React from 'react';
import { useSelector } from 'react-redux';
import { AiOutlineClose } from "react-icons/ai";
import './Trigger.css';

import cfg from '@/config';
const { agent_image } = cfg;

const Trigger = () => {
  const { images, trigger } = useSelector((state) => state.assistant);
  const assistantImage = images.png || (process.env.REACT_APP_URL + `/assets/images/${agent_image ?? process.env.REACT_APP_AGENT_IMAGE ?? 'agent-3'}.png`);

  return (
    <div className="embed-trigger">
      {trigger.active ? (
        <AiOutlineClose size={50} className="close-icon" />
      ) : (
        <img src={assistantImage} alt="Assistant" className="embed-trigger-image" draggable="false" />
      )}
    </div>
  );
};

export default Trigger;
