  import { createSlice } from "@reduxjs/toolkit";

  const chatSlice = createSlice({
    name: "chat",
    initialState: {
      history: [],
      input: {
        type: null,
        payload: {},
      },
    },
    reducers: {
      addMessage: (state, action) => {
        state.history.push(action.payload);
      },
      setChatInputType: (state, action) => {
        state.input.type = action.payload;
      },
      setChatInputPayload: (state, action) => {
        state.input.payload = action.payload;
      },
    },
  });

  export const { addMessage, setChatInputType, setChatInputPayload } = chatSlice.actions;
  export default chatSlice.reducer;
