import store from "@/store";
import { setInput, setType, setActive, setMode } from "@/store/slices/inputSlice";
import { setChatInputType, setChatInputPayload } from '@/store/slices/chatSlice';
import { addMessage } from "@/store/slices/chatSlice";

class InputService {

  // ------
  // Config
  // ------

  INPUT_HEIGHT_MAP = {
    default: ["choices", "form", "payment"],
    fullscreen: [],
  };

  OUTPUT_HANDLERS = {
    default: (...args) => console.log("Default output Handler: ", args)
  }

  // -----------
  // Constructor
  // -----------

  constructor(setup = true) {

    // Callables
    this.ws_send = console.log;
    this.send_text_message = console.log;

    // Bindings
    this.send_to_assistant = this.send_to_assistant.bind(this);

    // Setup
    if (setup) this.setup();
    
  }

  // -----
  // Setup
  // -----

  setup() {

    // Register Output Handlers
    this.OUTPUT_HANDLERS = {
      // choices: pl => this.send_to_assistant('choices', pl),
      'choices': pl => this.send_text_message(pl.message),
      'cards': pl => this.send_text_message(pl.message),
      'form.basic': pl => this.send_text_message(JSON.stringify(Object.keys(pl.formData).map(f => `${f} = ${pl.formData[f]}`).join('\n'))),
      'form.dynamic': pl => this.send_text_message(JSON.stringify(Object.keys(pl.formData).map(f => `${f} = ${pl.formData[f]}`).join('\n'))),
      'default': pl => this.send_text_message(`${JSON.stringify(pl)}`),
    };
  }

  // ---------
  // Set Input
  // ---------

  set_input({ type, ...payload }) {
    store.dispatch(setInput(payload));
    store.dispatch(setType(type));
    store.dispatch(setActive(true));

    // Determine input height
    const heightMode = this.INPUT_HEIGHT_MAP.default.includes(type) ? "default" : "fullscreen";
    store.dispatch(setMode(heightMode));
  }

  // --------------
  // Complete Input
  // --------------

  complete(type, payload, dispatch_output=true) {
    store.dispatch(setActive(false));
    store.dispatch(setInput(null));
    store.dispatch(setType(null));
    store.dispatch(setChatInputType(null));
    store.dispatch(setChatInputPayload({}));
    store.dispatch(setMode("default"));

    console.log("Completed = ", type, payload);

    if(type && dispatch_output){
      (this.OUTPUT_HANDLERS[type] ?? this.OUTPUT_HANDLERS.default)(payload);
    }
  }

  // --------
  // Handlers
  // --------

  send_to_assistant(type, payload) {
    console.log("Handling " + type + " input:", payload); // @debug
    this.ws_send({
      type: 'command',
      command: 'ui.output',
      payload: {
        type,
        payload
      }
    });
  }

  add_message(message) {
    store.dispatch(addMessage(message));
  }

}

// ------
// Export
// ------

const is = new InputService();
window.is = is; // @debug
export default is;
