import React from 'react';
import { Box } from '@mui/material';
import { useSelector } from 'react-redux';
import BasicAssistent from '@/components/assistants/BasicAssistant/BasicAssistent';
import ChatPanel from '@/components/panels/ChatPanel/ChatPanel';
// import InputPanel from '@/components/panels/InputPanel/InputPanel';
import './Container.css';

export default function Container({ chat = true ,voiceOnly}) {
  const { state } = useSelector((state) => state.input);
  const inputClass =
    state.active ? 'active' : state.height === 'fullscreen' ? 'fullscreen' : 'default';
 
  return (
    <Box className="container ea-line-bg">
      {/* <Box className={`input-panel ${inputClass}`}>
        {state.active && <InputPanel />}
      </Box> */}

      <Box className="assistant">
        <BasicAssistent voiceOnly={voiceOnly} />
      </Box>

      {chat && (
        <Box className="chat-panel">
          <ChatPanel />
        </Box>
      )}
    </Box>
  );
}
