import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Box } from "@mui/material";

import ChociesInput from './inputs/ChociesInput.js';
import BasicInput from './inputs/BasicInput.js';
import DefaultInput from './inputs/DefaultInput.js';
import CardsInput from './inputs/CardsInput.js';

import './ChatPanel.css';

export default function ChatPanel() {
  const { history, input: { type, payload } } = useSelector((state) => state.chat);
  

  useEffect(() => {
    const scrollHeightInterval = setInterval(() => {
      const chatPanel = document.querySelector('.ChatPanel');
      if ((chatPanel.scrollHeight - chatPanel.clientHeight - chatPanel.scrollTop) < 150){
        chatPanel.scrollTop = chatPanel.scrollHeight;
      }
    }, 1000);

    return () => clearInterval(scrollHeightInterval);
  }, []);


  console.log(type)
  const ChatInput = {
    choices: ChociesInput,
    basic: BasicInput,
    cards: CardsInput,
  }[type] ?? BasicInput;

  return (
    <Box
      className="ChatPanel"
      sx={{
        height: '100%',
          borderRadius: 4,
      }}
    >
      {history?.filter(m => m.message?.trim()).map((msg, idx) => (
        <div key={idx} className={"ChatPanel-item ChatPanel-message " + (msg.from === "user" ? "user" : "bot")}>
          <p>{msg.message}</p>
        </div>
      ))}
      <ChatInput payload={payload} />
    </Box>
  )
};