import React, { useState } from "react";
import "./CardsInput.css"; // Import external CSS
import vcs from '@/services/VoiceChatService';
const INPUT_STRING = "[INPUT]";

const CardsInput = ({ payload }) => {
  const [sent, setSent] = useState(false);
  const [activeCard, setActiveCard] = useState(0); // Active card at index 0
  const { cards, format } = payload;

  const formatAndSend = (input) => {
    setSent(true);
    const messageToSend = format.replaceAll(INPUT_STRING, input);
    vcs.send_text_message(messageToSend);
  };

  if (sent) return null;

  return (
    <div className="ChatPanel-cards">
      <div className="ChatPanel-cards-slider">
        {cards.map((card, index) => (
          <div
            key={index}
            className={`ChatPanel-cards-slider-item ${
              index === activeCard ? "ChatPanel-cards-slider-item-active" : ""
            }`}
          >

            {/* Image Container */}
            <div className="ChatPanel-cards-image-container">
              <img src={card.picture} alt={card.label} className="ChatPanel-cards-image" />

              {/* Left Button */}
              {index > 0 && (
                <button
                  className="ChatPanel-cards-slider-btn ChatPanel-cards-slider-btn-left"
                  onClick={() => setActiveCard(activeCard - 1)}
                >
                  
                </button>
              )}
              {/* Right Button */}
              {index < cards.length - 1 && (
                <button
                  className="ChatPanel-cards-slider-btn ChatPanel-cards-slider-btn-right"
                  onClick={() => setActiveCard(activeCard + 1)}
                >
                  
                </button>
              )}

              {/* Label */}
              <div className="ChatPanel-cards-label">{card.label}</div>
            </div>

            {/* Options Section - Now below the picture */}
            <div className="ChatPanel-cards-options">
              {card.options.map((option, idx) => (
                <button
                  key={idx}
                  className="ChatPanel-cards-option"
                  onClick={() => formatAndSend(option.value)}
                >
                  {option.label}
                </button>
              ))}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default CardsInput;
