import React, { useRef, useEffect } from 'react';
import { gsap } from 'gsap';
import './Slideup.css';

export default function Slideup({ className, Trigger, children, active, setActive }) {
  const panelRef = useRef(null);

  useEffect(() => {
    if (active && panelRef.current) {
      gsap.set(panelRef.current, { scale: 0, opacity: 0 });
      gsap.to(panelRef.current, {
        scale: 1,
        opacity: 1,
        duration: 0.8,
        ease: 'power3.out'
      });
    }
  }, [active]);

  // Open the panel
  const openPanel = () => setActive(true);

  // Close the panel with an animation and then update the state.
  const closePanel = () => {
    if (panelRef.current) {
      gsap.to(panelRef.current, {
        scale: 0,
        opacity: 0,
        duration: 0.8,
        ease: 'power3.in',
        onComplete: () => setActive(false)
      });
    }
  };

  // Toggle panel open/close.
  const togglePanel = () => (active ? closePanel() : openPanel());

  return (
    <div className={`Slideup ${active ? 'active' : ''} ${className || ''}`}>
      {/* Trigger Button */}
      <div className="Slideup-trigger" onClick={togglePanel}>
        <Trigger slideupActive={active} toggleSlideup={togglePanel} />
      </div>

      {/* Animated Panel */}
      <div className="Slideup-panel">
        <div
          className="Slideup-modal"
          ref={panelRef}
          style={{
            top: 0,
            left: 0,
            transformOrigin: 'bottom right' // Ensures the animation originates from bottom-right.
          }}
        >
          {children}
        </div>
      </div>
    </div>
  );
}
