import { createSlice } from "@reduxjs/toolkit";

const assistantSlice = createSlice({
  name: "assistant",
  initialState: {
    images: {
      gif: null,
      png: null,
    },
    state: {
      is_speaking: false,
      is_recording: false,
      is_muted: false,
    },
    trigger: {
      active: false,
    },
  },
  reducers: {
    setImages: (state, action) => {
      state.images = action.payload;
    },
    setSpeaking: (state, action) => {
      state.state.is_speaking = action.payload;
    },
    setRecording: (state, action) => {
      state.state.is_recording = action.payload;
    },
    setMuted: (state, action) => {
      state.state.is_muted = action.payload;
    },
    setTriggerActive: (state, action) => {
      state.trigger.active = action.payload;
    },
  },
});

export const { setImages, setSpeaking, setRecording, setMuted, setTriggerActive } = assistantSlice.actions;
export default assistantSlice.reducer;
