import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { setChatInputType, setChatInputPayload } from '@/store/slices/chatSlice';
import vcs from '@/services/VoiceChatService';
import './BasicInput.css';

const INPUT_STRING = '[INPUT]';

const BasicInput = ({ payload = {} }) => {
  const dispatch = useDispatch();
  const [inputValue, setInputValue] = useState('');
  
  // Provide default values to ensure format is defined
  const { label = 'Type here:', format = '  [INPUT]' } = payload;

  const formatAndSend = () => {
    // Check if the input is non-empty
    if (!inputValue.trim()) return;
    
    // Replace the placeholder with the actual input value
    const messageToSend = format.replaceAll(INPUT_STRING, inputValue);
    vcs.send_text_message(messageToSend);

    // Optionally, clear the input after sending
    setInputValue('');

    // Reset chat input state in Redux
    dispatch(setChatInputType(null));
    dispatch(setChatInputPayload({}));
  };

  return (
    <div className="BasicInput-buttons">
      <label className="BasicInput-label">{label}</label>
      <div className="BasicInput-group">
        <input
          type="text"
          className="BasicInput-input"
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          onKeyDown={(e) => e.key === 'Enter' && formatAndSend()}
        />
        <button className="BasicInput-send" onClick={formatAndSend}>
          =&gt;
        </button>
      </div>
    </div>
  );
};

export default BasicInput;
